body {
  font-family: 'Segoe UI', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f5f7fa;
  color: #333;
}

.navbar {
  background-color: #0d1117;
  padding: 1rem;
  position: sticky;
  top: 0;
  z-index: 100;
}

.navbar ul {
  list-style: none;
  display: flex;
  justify-content: center;
  gap: 2rem;
  padding: 0;
  margin: 0;
}

.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-weight: bold;
}

.navbar a:hover {
  color: #61dafb;
  transition: 0.3s ease;
}

.section {
  padding: 4rem 2rem;
  text-align: center;
}

.project-card {
  margin: 2rem auto;
  padding: 1rem;
  max-width: 600px;
  border: 1px solid #ccc;
  border-radius: 10px;
}

button {
  background-color: #0d1117;
  color: white;
  padding: 0.7rem 1.2rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
}

button:hover {
  background-color: #61dafb;
  color: #0d1117;
  transition: 0.3s ease;
}
